@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&family=Noto+Sans+KR:wght@300;400;500;700;900&family=Open+Sans:wght@300;400;600;700&display=swap");

/*  */
html,
body,
button,
input,
select,
textarea {
  font-family: "Noto Sans KR", Tahoma, Geneva, sans-serif;
  -webkit-font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: unset;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: antialiased;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  backface-visibility: hidden;
  text-rendering: auto;
  text-rendering: optimizeSpeed;
  text-rendering: optimizeLegibility;
  text-rendering: geometricPrecision;
  text-rendering: inherit;
}
html {
  /*overflow-x:hidden;*/
}
html,
body {
  margin: 0 auto;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.4;
  color: #000;
  -webkit-text-size-adjust: none;
  word-break: keep-all;
  word-wrap: break-word;
}
body {
  position: relative;
}

/* input */
input:focus,
button:focus,
textarea:focus,
select:focus {
  outline: none;
}
input {
  font-size: 14px;
  -webkit-border-radius: 0;
  vertical-align: middle !important;
}
button {
  vertical-align: middle;
}
select {
  -webkit-border-radius: 0;
  vertical-align: middle;
}
button,
input {
  letter-spacing: 0px;
}
select,
input,
textarea {
  color: #111;
  vertical-align: middle;
}

input::placeholder,
textarea::placeholder {
  color: #a3a3a3;
  opacity: 1;
}
::-webkit-input-placeholder {
  color: #a3a3a3;
  letter-spacing: -1px;
  font-weight: normal;
}
::-moz-placeholder {
  color: #a3a3a3;
  letter-spacing: -1px;
  font-weight: normal;
}
:-ms-input-placeholder {
  color: #a3a3a3;
  letter-spacing: -1px;
  font-weight: normal;
}
:-moz-placeholder {
  color: #a3a3a3;
  letter-spacing: -1px;
  font-weight: normal;
}
input::-ms-clear,
input::-ms-reveal {
  display: none;
}

/*
input{-webkit-appearance:none;-moz-appearance:none;appearance:none;}
*/
select::-ms-expand {
  display: none;
}
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
input[type="text"],
input[type="password"],
input[type="tel"],
input[type="number"],
input[type="email"],
textarea {
  height: 34px;
  padding: 0 10px;
  font-weight: 400;
  color: #111;
  border: 1px solid #e0e0e0;
}

