/* button */
.btn {
  display: inline-block;
  text-align: center;
  padding: 0 10px;
  line-height: 26px;
  border-radius: 3px;
  font-size: 14px;
  color: #9b9b9b;
  background: #f4f4f4;
  border: 0.08rem solid #e3e3e3;
  vertical-align: middle;
}

/* line */
.line01 {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 1.4em;
  word-wrap: break-word;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.line02 {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 3em;
  word-wrap: break-word;
