/* 랜드바이저 맵 */
.fullMapArea > .mainMapPopTit {
  position: absolute;
  left: 50%;
  top: 27px;
  padding: 4px 20px;
  font-weight: 400;
  border-radius: 99px;
  transform: translateX(-50%);
  box-shadow: 0px 4px 17px 3px rgba(0, 0, 0, 0.13);
  z-index: 4;
  color: #010101;
  text-align: center;
  line-height: 30px;
  background-color: rgba(255, 255, 255, 0.8);
}

.btn-sch-toggle {
  width: 20px;
  height: 20px;
  background: url("../images/ico-toggle-arrow.png") 50% / 11px no-repeat;
  transform: rotate(0);
  transition: 0.4s;
  text-indent: -9999em;
}
.btn-sch-toggle.active {
  background-image: url("../images/ico-toggle-arrow-active.png");
  transform: rotate(-90deg);
}
.fullMapArea-bx01 {
  overflow: hidden;
  position: absolute;
  left: 16px;
  top: 16px;
  width: 400px;
  background: #fff;
  border-radius: 10px;
  z-index: 4;
}
.fullMapArea-bx01 .bx01 {
  padding: 15px;
  border-bottom: 1px solid #d9d9d9;
}
.fullMapArea-bx01 .bx01 p {
  position: absolute;
  right: 10px;
  top: 15px;
  color: #545454;
  font-size: 12px;
  line-height: 24px;
}
.fullMapArea-bx01 .bx01 .toggleControl {
  position: relative;
  display: inline-block;
  margin: 0 2px;
  padding-left: 40px;
  height: 20px;
  cursor: pointer;
  user-select: none;
  background: #fff;
}
.fullMapArea-bx01 .bx01 .toggleControl input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.fullMapArea-bx01 .bx01 .control {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 40px;
  border: 2px solid #d1d1d1;
  border-radius: 99px;
}
.fullMapArea-bx01 .bx01 .control:after {
  content: "";
  position: absolute;
  left: 2px;
  top: 2px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #078274;
  transition: left 0.15s ease-in;
  border: none;
  -webkit-box-shadow: 0px 1px 10px 3px rgba(0, 0, 0, 0.19);
  box-shadow: 0px 1px 10px 3px rgba(0, 0, 0, 0.19);
}
.fullMapArea-bx01 .bx01 .toggleControl input:checked + .control {
}
.fullMapArea-bx01 .bx01 .toggleControl input:checked + .control:after {
  left: 22px;
  content: "";
  background: #078274;
}
.fullMapArea-bx01 .bx01 select {
  height: 30px;
  padding: 0 0 0 5px;
  font-size: 14px;
  font-weight: 400;
}

.fullMapArea-bx01 .bx01 .control2 {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 40px;
  border: 2px solid #d1d1d1;
  border-radius: 99px;
}
.fullMapArea-bx01 .bx01 .control2:after {
  content: "";
  position: absolute;
  left: 2px;
  top: 2px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #078274;
  transition: left 0.15s ease-in;
  border: none;
  -webkit-box-shadow: 0px 1px 10px 3px rgba(0, 0, 0, 0.19);
  box-shadow: 0px 1px 10px 3px rgba(0, 0, 0, 0.19);
}
.fullMapArea-bx01 .bx01 .toggleControl input:checked + .control2 {
}
.fullMapArea-bx01 .bx01 .toggleControl input:checked + .control2:after {
  left: 22px;
  content: "";
  background: #ccc;
}

.fold-bx {
  display: block;
  overflow-y: auto;
  transition: 1s;
}

.fold-bx2 {
  display: none;
}

/* 검색조건 > 항목 */
.bx03 {
  height: 400px;
  display: flex;
}
.item-cate {
  height: 100%;
  width: 90px;
  padding: 4px 0;
  border-right: 4px solid #e5e5e5;
}
.item-cate li a {
  overflow: hidden;
  display: block;
  padding: 8px 12px;
  color: #474646;
  text-align: center;
}
.item-cate li a.active {
  margin-right: -4px;
  color: #078274;
  font-weight: 500;
  background: #e6f2f1;
  border-right: 4px solid #078274;
}
.item-view {
  width: calc(100% - 90px);
  padding: 15px;
  max-height: 500px;
}
.item-view h3 {
  position: relative;
  margin-bottom: 5px;
  font-size: 13px;
}
.item-view h3 .btn-all-bx {
  position: absolute;
  right: 0;
  bottom: 0;
  color: #bbb;
  font-size: 10px;
  background: none;
}
.fullMapArea .btn-all-bx input[type="checkbox"]:checked + label {
  color: #078274;
  font-weight: 400;
}
.item-view ul {
  display: flex;
  flex-wrap: wrap;
}
.item-view ul li {
  width: calc(100% / 3);
}
.item-view > div {
  /* display: none;*/
  margin-bottom: 15px;
}
.item-view > div + div {
  margin-top: 0px;
}
.fullMapArea .item-view li input[type="radio"] + label,
.fullMapArea .item-view li input[type="checkbox"] + label {
  display: block;
  width: 100%;
  padding: 4px 5px;
  text-align: center;
  background: #f2f2f2;
  border: 1px solid #d9d9d9;
  font-size: 12px;
}
.fullMapArea .item-view li input[type="radio"]:checked + label,
.fullMapArea .item-view li input[type="checkbox"]:checked + label {
  background: #e6f2f1;
}

/* 검색조건 > input radio 커스텀 */
.fullMapArea input[type="radio"],
.fullMapArea input[type="checkbox"] {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  line-height: normal;
  font-size: 0;
}
.fullMapArea input[type="radio"] + label,
.fullMapArea input[type="checkbox"] + label {
  display: inline-block;
  position: relative;
  padding-left: 28px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
.bx-bottom input[type="checkbox"] + label:before {
  content: "";
  position: absolute;
  right: 0;
  top: 2px; /* 이 값을 변경해서 글자와의 정렬 */
  width: 16px; /* 체크박스의 너비를 지정 */
  height: 16px; /* 체크박스의 높이를 지정 */
  line-height: 16px; /* 세로정렬을 위해 높이값과 일치 */
  text-align: center;
  border: 1px solid #cacece;
  border-radius: 0;
  box-shadow: none;
  border-radius: 3px;
  background: #ddd url("../images/ico-check.png") 50% / 14px no-repeat;
}
.bx-bottom input[type="checkbox"]:checked + label:before {
  content: "";
  color: #fff;
  box-shadow: none;
  background-color: #0f3471;
}
.bx-bottom {
  padding: 15px 0 15px 110px;
  font-size: 13px;
  background: #eee;
  text-align: left;
}
.bx-bottom li {
  display: inline-block;
  font-weight: 300;
}
.bx-bottom li + li {
  margin-left: 10px;
}
.bx-bottom li:nth-child(1) {
  color: #940d0d;
}
.bx-bottom li:nth-child(2) {
  color: #078274;
}
.bx-bottom li:nth-child(3) {
  color: #213e95;
}
.fullMapArea .bx-bottom input[type="checkbox"] + label {
  padding: 0 26px 0 0;
}
.bx-bottom li:nth-child(1) input[type="checkbox"]:checked + label:before {
  background-color: #940d0d;
  border-color: #940d0d;
}
.bx-bottom li:nth-child(2) input[type="checkbox"]:checked + label:before {
  background-color: #078274;
  border-color: #078274;
}
.bx-bottom li:nth-child(3) input[type="checkbox"]:checked + label:before {
  background-color: #213e95;
  border-color: #213e95;
}

/* 위젯 */
.fullMapArea-widget {
  overflow: hidden;
  position: absolute;
  right: 16px;
  top: 16px;
  width: 55px;
  background: #fff;
  border-radius: 10px;
  z-index: 4;
  box-shadow: 0px 9px 10.79px 2.21px rgba(0, 0, 0, 0.17);
}
.fullMapArea-widget li {
  padding: 8px;
}
.fullMapArea-widget li + li {
  border-top: 1px solid #dfe5eb;
}
.fullMapArea-widget input[type="radio"] + label,
.fullMapArea-widget input[type="checkbox"] + label {
  display: block;
  width: 100%;
  padding: 0;
  font-size: 10px;
  color: #000;
  text-align: center;
}
.fullMapArea-widget input[type="radio"] + label:before,
.fullMapArea-widget input[type="checkbox"] + label:before {
  content: "";
  display: block;
  height: 26px;
}
.fullMapArea-widget input[type="radio"]:checked + label,
.fullMapArea-widget input[type="checkbox"]:checked + label {
  color: #0096a2;
}
.fullMapArea-widget li:nth-child(4) input[type="checkbox"] + label:before {
  background: url("../images/ico-widget04.png?2022-10-12") 50% no-repeat;
}
.fullMapArea-widget
  li:nth-child(4)
  input[type="checkbox"]:checked
  + label:before {
  background: url("../images/ico-widget04-active.png?2022-10-12") 50% no-repeat;
}
.fullMapArea-widget input[type="radio"]:checked + label,
.fullMapArea-widget input[type="checkbox"]:checked + label {
  color: #0096a2;
}
.fullMapArea-widget button {
  width: 100%;
  height: 26px;
  font-weight: 300;
  background: #fff;
  color: #000;
  font-size: 13px;
}
.fullMapArea-widget li.active {
  background: #e6f2f1;
}
.fullMapArea-widget li.active button {
  font-weight: 500;
  background: #e6f2f1;
  color: #078274;
}
.label {
  margin-bottom: 96px;
}
