.ir {
  display: inline-block;
  vertical-align: middle;
}
:before {
  margin-top: -2px;
  vertical-align: middle;
}
.bgOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.25);
  z-index: 990;
}
.brRound {
  border-radius: 99px;
}
.bgBasic {
  background-color: #0fa4a0;
}
.bgBasicBlank {
  color: #0fa4a0 !important;
  background-color: #fff;
  border: 1px solid #0fa4a0;
}
.bgGray {
  background-color: #f0f0f0;
}
.bgRed {
  color: #fff !important;
  background-color: #ad0000;
}
.bxShadow {
  -webkit-box-shadow: 0px 4px 10px 4px rgba(0, 0, 0, 0.23);
  box-shadow: 0px 4px 10px 4px rgba(0, 0, 0, 0.23);
}
.bxRound,
.topSch,
.topSch input[type="text"],
.topSch .btnSch,
.topSchList,
.loginForm p input,
.loginForm p .btnLogin,
.snsLogin a,
.areaItem .myValue,
.areaItem ul {
  border-radius: 4px;
}

input[type="text"],
input[type="password"],
input[type="tel"],
input[type="email"],
input[type="number"] {
}

select:focus,
textarea:focus,
input[type="text"]:focus,
input[type="password"]:focus,
input[type="tel"]:focus,
input[type="email"]:focus,
input[type="number"]:focus {
  border: 1px solid #03b3a0 !important;
}

input[type="text"],
input[type="password"],
input[type="tel"],
input[type="email"],
input[type="number"],
textarea {
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  outline: none;
}

input.fc:focus {
  -webkit-box-shadow: 0px 0px 23.49px 3.51px rgba(69, 219, 208, 0.5);
  -moz-box-shadow: 0px 0px 23.49px 3.51px rgba(69, 219, 208, 0.5);
  box-shadow: 0px 0px 23.49px 3.51px rgba(69, 219, 208, 0.5);
  border: 3px solid #11d2bc !important;
}

@keyframes bounce {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-10px);
  }
}
@-webkit-keyframes bounce {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-10px);
  }
}

body {
  padding-top: 56px;
  letter-spacing: -1px;
}
.bgBasic {
}
/* 버튼 over */
.btnBox a:hover,
.bgBasic:hover,
.btnLogout:hover {
  /*background:rgba(15,164,160,.8);*/
}
.btnBox a:hover,
.bgBasic:hover,
.btnLogout:hover,
.btnLogoutSide:hover,
.loginForm p .btnLogin:hover {
  opacity: 0.7;
  transition: 100ms linear;
}

/* header */
header {
  position: fixed;
  top: 0;
  width: 100%;
  /*height: 54px;*/
  background: #fff;
  z-index: 50;
  -webkit-box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.08);
  box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.08);
}
header.noshadow {
  border-bottom: 1px solid #e2e2e2;
  -webkit-box-shadow: none;
  box-shadow: none;
}
header .inner {
  position: relative;
  padding: 8px 20px;
  /*border: 1px solid red;*/
}
header .logo {
  margin-top: -4px;
}
header .logo img {
  width: 32px;
  height: 36px;
}
.topSchBox {
  position: relative;
  width: 365px;
  margin: auto;
}
.topSch {
  overflow: hidden;
  width: 540px;
}

.topSch input[type="text"] {
  float: left;
  width: 370px;
  padding-right: 20px;
  height: 40px;
  font-size: 16px;
  color: #000;
  font-weight: 500;
  border: 1px solid #cecece;
  border-radius: 4px;
}
.topSch .btnSch {
  position: absolute;
  right: 2px;
  top: 5px;
  overflow: hidden;
  width: 30px;
  height: 30px;
  text-indent: -9999em;
  background: #fff url(../images/ico_sch.png) 50% no-repeat;
  outline: none;
  border-radius: 0;
}
.topSchList {
  display: none;
  position: absolute;
  width: 100%;
  padding: 1px;
  border: 1px solid #e3e3e3;
  z-index: 90;
  background-color: #fff;
}
.topSchList ul {
  max-height: 350px;
  overflow-y: auto;
}
.topSchList ul li {
  padding: 8px 10px;
  color: #9c9c9c;
  cursor: pointer;
}
.topSchList ul li span {
  color: #000;
}
.topSchList ul li:hover {
  color: #fff;
  background-color: #03b3a0;
}
.topSchList ul li:hover span {
  color: #fff;
}
.topSchList li:before {
  content: "";
  display: inline-block;
  width: 15px;
  height: 18px;
  margin-right: 7px;
  background: url(../images/ico-pin.png) 100% 0 no-repeat;
}
.topSchList li:hover:before {
  background-position: 0 0;
}
.topSch.result {
  /*border:1px solid #cecece;background:#f2f2f2;*/
}
.topSch.result:hover {
  /*border:1px solid #cecece;*/
}
.topSch.result input {
  border: 1px solid #cecece;
  background-color: #f2f2f2;
}
.topSch.result button,
topSch.result .btnSch.basic {
  border: 1px solid transparent;
  background-color: transparent;
}
.topSch.result input.basic {
  border: 1px solid #cecece;
  background-color: #fff;
}
/*.topSch input[type=text]:focus{border:1px solid #03b3a0;}*/

.topSch.result button.vClear {
  position: absolute;
  right: 46px;
  top: 8px;
  width: 24px;
  height: 24px;
  text-indent: -9999em;
  border-radius: 50%;
  background: #c3c3c3 url(../images/vclose.png) 50% no-repeat;
  z-index: 1;
}
.topSch.result:after {
  content: "";
  position: absolute;
  right: 38px;
  top: 6px;
  display: inline-block;
  width: 1px;
  height: 26px;
  background-color: #cecece;
}
.LoginBtnBox {
  position: absolute;
  right: 15px;
  top: 24px;
  padding-right: 85px;
  color: #7f7f7f;
}
.LoginBtnBox p {
  font-size: 12px;
  text-align: right;
}
.LoginBtnBox p:before {
  display: none;
}
.LoginBtnBox p span {
  font-size: 14px;
}
.LoginBtnBox a em {
  color: #000;
  font-weight: 500;
}
.LoginBtnBox a:hover em {
  border-bottom: 1px solid #000;
}
.btnLoginPop {
  position: absolute;
  display: block;
  right: 83px;
  top: 24px;
  width: 73px;
  height: 30px;
  color: #fff;
  text-align: center;
  line-height: 30px;
}
.btnLogout,
a.btnLogout {
  position: absolute;
  display: block;
  right: 0;
  top: 8px;
  width: 60px;
  height: 20px;
  color: #00b99a;
  font-size: 12px;
  text-align: center;
  line-height: 18px;
  border: 1px solid #00b99a;
  border-radius: 99px;
}

.btnMenuBox {
  position: absolute;
  display: block;
  top: 0;
  right: -10px;
  width: 70px;
  height: 84px;
  cursor: pointer;
}
.btnMenuBox:before {
  content: "";
  position: absolute;
  left: 0;
  top: 27px;
  display: block;
  width: 1px;
  height: 33px;
  background-color: #c9c9c9;
}
.btnMenu {
  width: 23px;
  height: 19px;
  margin: auto;
  position: absolute;
  right: 22px;
  top: 50%;
  margin: -10px 0 0 0;
  text-indent: -9999em;
  background: url(../images/btn_menu.png) no-repeat;
}
.btnMenu.active {
  background: url(../images/btn_menu_over.png) no-repeat;
}
.sideMenu {
  display: none;
  position: absolute;
  top: 82px;
  right: 0;
  width: 193px;
  padding: 0 1px;
  font-size: 16px;
  background: #fff;
  border-top: 1px solid #c1c1c1;
  box-shadow: 0px 4px 10px 0.5px rgba(0, 0, 0, 0.4);
}
.sideMenu.active:before {
  content: "";
  position: absolute;
  top: 0;
  right: 33px;
  width: 13px;
  height: 7px;
  margin: -7px 0 0;
  background: url(../images/box_arrow_02_gray.png) 0 0 no-repeat;
}
#introWrap .sideMenu {
  border-top: 1px solid #05b3a1;
}
#introWrap .sideMenu.active:before {
  background: url(../images/box_arrow_02.png) 0 0 no-repeat;
}
.sideMenu ul {
  padding: 15px 0;
  z-index: 999;
}
.sideMenu li {
  padding: 10px 0;
}
.sideMenu li a {
  display: block;
  padding: 0 34px;
  font-weight: 300;
}
.sideMenu li a:before {
  content: "";
  display: inline-block;
  width: 5px;
  height: 5px;
  margin: -4px 14px 0 0;
  background: #c1c1c1;
}
.sideMenu li.active,
.sideMenu li.over {
  color: #0fa4a0;
  background: #e4eded;
  transition: 100ms linear;
}
.sideMenu li.active a,
.sideMenu li.over a {
  color: #0fa4a0;
}
.sideMenu li.active a:before,
.sideMenu li.over a:before {
  background: #0fa4a0;
}
a.btnLogoutSide {
  display: block;
  color: #0fa4a0;
  margin: 0 5px 5px;
  line-height: 37px;
  text-align: center;
  border: 1px solid #0fa4a0;
}

/* 레이어팝업 공통 */
.popLayer {
  position: fixed;
  background: #fff;
  border: 1px solid #e2e2e2;
  -webkit-box-shadow: 0px 9px 13px 17px rgba(0, 0, 0, 0.17);
  box-shadow: 0px 9px 13px 17px rgba(0, 0, 0, 0.17);
  z-index: 999;
}
.btnBox {
  text-align: center;
}
.boxDeco {
}
.boxDeco:before {
  content: "";
  display: block;
  width: 57px;
  height: 6px;
  margin: auto;
  background: #0fa4a0;
}
/* 로그인 레이어 */
.popLayer.popLayerLogin {
  left: 50%;
  top: 80px;
  width: 1024px;
  transform: translateX(-50%);
  -webkit-box-shadow: none;
  box-shadow: none;
}
.popLayerInner {
  width: 310px;
  padding: 40px 0 60px;
  margin: auto;
}
.loginForm {
}
.loginForm p {
  margin-bottom: 12px;
}
.loginForm p input {
  width: 100%;
  height: 37px;
  color: #000;
  border: 1px solid #cecece;
}
.loginForm p:last-child {
  margin: 6px 0 18px;
}
.loginForm p .btnLogin {
  width: 100%;
  height: 42px;
  color: #fff;
  background: #03b3a0;
}
.snsLogin {
  padding-top: 23px;
  border-top: 1px solid #ccc;
}
.snsLogin a {
  display: block;
  padding: 0 16px;
  font-weight: 400;
  text-align: center;
  line-height: 45px;
  border: 1px solid #cecece;
  transition: 100ms linear;
}
.snsLogin .ir {
  margin: -3px 8px 0 0;
}
a.btnLoginNaver {
  color: #fff;
  background: #00db52;
}
a.btnLoginKakao {
  color: #3c1e1e;
  background: #fee500;
}
a.btnLoginFacebook {
  color: #fff;
  background: #3c579e;
}
a.btnLoginGoogle {
  color: #fff;
  background: #4186f5;
}
a.btnLoginNaver:hover {
  color: #fff;
  background: #00e855;
}
a.btnLoginKakao:hover {
  color: #3c1e1e;
  background: #ffea23;
}
a.btnLoginFacebook:hover {
  color: #fff;
  background: #3c63b5;
}
a.btnLoginGoogle:hover {
  color: #fff;
  background: #3793fa;
}

.snsLogin li + li {
  margin-top: 11px;
}
.findId {
  overflow: hidden;
  padding: 36px 0 0;
}
.findId a {
  color: #545454;
}
.findId a:hover {
  text-decoration: underline;
}
.findId ul {
  float: left;
}
.findId ul li {
  display: inline-block;
}
.findId ul li + li:before {
  content: "";
  display: inline-block;
  width: 1px;
  height: 13px;
  margin: 0 7px 0 5px;
  background: #828282;
}
.findId .join {
  float: right;
  color: #000;
}
.findId .ir {
  width: 10px;
  height: 14px;
  margin: -2px 5px 0 0;
}
.popCloseBtn {
  position: absolute;
  right: 72px;
  top: 36px;
  width: 26px;
  height: 26px;
  text-indent: -9999em;
  background: url(../images/ico_close_pop.png) no-repeat;
}
.popCloseBtn2 {
  position: absolute;
  right: 72px;
  top: 36px;
  width: 26px;
  height: 26px;
  text-indent: -9999em;
  background: url(../images/ico_close_pop.png) no-repeat;
  cursor: pointer;
} /* 탁상감정의뢰 취소버튼 */
